body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: rgb(244, 244, 244, .5);

  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  flex-flow: column nowrap;

  position: relative;
}
.header {
    position: absolute;
    top: -8vmin;
    left: -5%;
    width: 110%;
    border-radius: 5px;
    border: 1px solid rgba(52, 71, 107, .2);   
    height: 15vmin;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1vmin 5% 8vmin 5%;
    box-sizing: border-box;
}

.header h1 {
    padding: 0;
    margin: 0;
    text-align: center;
    color: #3E130A;
    font-family: 'futura lt';
    font-size: 30px;
    font-weight: 400;
}
.social {
}
.social ul, li, a {
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    margin: .8vmin;
}
.social img {
    width: 3vmin;
    height: 3vmin;
    padding: 0;
    margin:0;
    -webkit-filter: brightness(0) invert(10%) sepia(73%) saturate(915%) hue-rotate(335deg) brightness(97%) contrast(101%);
            filter: brightness(0) invert(10%) sepia(73%) saturate(915%) hue-rotate(335deg) brightness(97%) contrast(101%);
}
.social a:hover img {
    -webkit-filter: invert(66%) sepia(8%) saturate(3929%) hue-rotate(308deg) brightness(94%) contrast(85%);
            filter: invert(66%) sepia(8%) saturate(3929%) hue-rotate(308deg) brightness(94%) contrast(85%);
}
.footer {
    /* background: rgb(244, 244, 244); */
    position: absolute;
    bottom: -8vmin;
    left: -5%;
    width: 110%;
    border-radius: 5px;
    /* box-shadow: 0 4px 6px rgba(0,0,0,.2); */
    border: 1px solid rgba(52, 71, 107, .2);
    height: 15vmin;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    padding: 1vmin;
    box-sizing: border-box;
}

.footer p {
    padding: 0;
    margin: 0;
    text-align: center;
    
    font-family: 'futura lt';
    font-size: 19px;
    color: rgb(127, 127, 127);
}
.debate_detail {
  background: rgb(68, 47, 28);
  min-height: 70vmin;
  width: 95vmin;
  border-radius: 5px;
  box-shadow: 0 4px 11px rgba(0, 0, 0, .3);
  background: url(/static/media/me.aee1fa5c.jpg);
  background-size: cover;
  background-repeat: no-repeat;

  display: flex;
  justify-content: space-between;
  align-items: stretch;
  flex-flow: column nowrap;
  z-index: 1;
  padding: 2vmin 3vmin;
  box-sizing: border-box;
  position: relative;
}

.debate_detail::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: -webkit-linear-gradient(rgba(0, 0, 0, .3), rgba(0, 0, 0, .8));
  background: linear-gradient(rgba(0, 0, 0, .3), rgba(0, 0, 0, .8));
  content: "";
  display: block;
  border-radius: 5px;

}

.debate_detail * {
  z-index: 1
}

.debate_detail h2 {
  font-weight: 400;
  max-width: 75%;
  margin: 0;
  padding: 0;
  text-transform: lowercase;
  color: white;
}

.debate_detail h2 span {
  background: rgb(53, 71, 106);
  padding: 0 .2em;
  margin: 0 .2em 0 0;
  color: white;
}


.more {
  background: rgba(255, 255, 255, 0.6);
  width: auto;
  margin: 0 auto;
  border: 0;
  outline: 0;
  border-radius: 2px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .3);
  cursor: pointer;
}

.more:hover {
  background: white
}

.ideas {
    color: white;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    align-items: center;
    flex: 1 1
  }

/* rgb(62, 19, 10) */
/* rgb(223, 122, 130) */
/* rgb(45, 134, 178) */
/* rgb(236, 207, 163) */
.idea {
    border-radius: 5px;
    max-width: 45%;
    padding: 1vmin;
    background: rgba(255, 255, 255, 0.794);
    color: rgb(62, 19, 10);
  }
  
  .idea:hover {
    background: white;
  }
  
  .idea h3 {
    font-weight: 400;
    font-size: 16px;
    margin: 0 0 .5em 0;
    border-radius: 1px;
    padding: 0;
    position: relative;
  }
  .idea h3::after {
    content: "";
    width: 50%;
    height: 1px;
    background: rgba(62, 19, 10, .2);
    display: block;
    position: absolute;
    left: .3em;
    bottom: -.5em;
  }
  
  .idea h3 span {
    background: rgb(42, 35, 32);
    padding: 0 .5em 0 .2em;
    margin: 0 .5em 0 0;
    color: white;
  }
  
  .idea p {
    font-size: 12px;
    margin: 0;
    padding: 0 1vmin 0 .5vmin;  
    color: rgb(62, 19, 10);
  }
  
.debates {
  background: rgb(68, 47, 28);
  min-height: 70vmin;
